import React, {useState} from "react";
import {
  BrowserRouter as Router,
  HashRouter,
  Switch,
  Route
} from "react-router-dom";
import './App.scss';
import MenuList from './Components/list-menu';
import Footer from './Components/footer';
import Home from './Components/home/Home';
import MyMeal from './Components/day-001/MyMeal';
import Count from './Components/day-002/Count';

function App() {
  let data = [
    {
       "project_id": "000",
       "project_name": "Home",
       "project_component": Home,
       "project_url": "",
       "project_description": "Started this to stay in touch with the coding when I can! #100Days100Projects"
     },
    {
       "project_id": "001",
       "project_name": "Random Meal",
       "project_component": MyMeal,
       "project_url": "day-001",
       "project_description": "Fetching data from an API and show on click of button."
     },
    {
       "project_id": "002",
       "project_name": "Visitor Count",
       "project_component": Count,
       "project_url": "day-002",
       "project_description": "Update the count on every page refresh and keeping a track of visitor count on page. Added amenu for easier navigation of projects and changed the routes to has to support Github üòû"
     }
 ];
  return (
    <div className="App">
      <HashRouter basename="/coding-challenge">
        <>
          <MenuList menu={data} />
          <Switch>            
            {
              data.map((value, key)=>(
                // console.log(value.project_component),
                <Route exact path={`/${value.project_url}`} key={key}>
                  {
                    (value.project_id==="000")?
                    React.createElement(value.project_component, {
                      to : value.url, number : value.project_id, project : JSON.stringify(data)
                    }):
                    React.createElement(value.project_component, {
                      to : value.url, number : value.project_id
                    })
                  }
                </Route>
              ))
            }
          </Switch>
          <Footer />
        </>
      </HashRouter>
    </div>
  );
}

export default App;
