import './Project.scss';
import React, { useState } from 'react';
import Header from '../header';

function Home(props) {
    let data = JSON.parse(props.project);
    return (
        <>
            <Header number={props.number}></Header>
            <div className="App-container" style={{display: 'flex', alignItems: 'center', justifyContent: 'center', height: 'calc(100vh - 30px)', padding: '15px'}}>
                <table border="1" collapse="true">
                    <tr>
                        <th>#</th>
                        <th>Project Name</th>
                        <th>Project Description</th>
                    </tr>
                    <tr></tr>
                    {
                        data.map((value, key)=>(
                            <tr key={key}>
                                <td>{value.project_id}</td>
                                <td>{value.project_name}</td>
                                <td>{value.project_description}</td>
                            </tr>
                        ))
                    }
                </table>
            </div>
        </>
    );
}

export default Home;
